.projects {
  background-color: #f5f5f5;
}

.projects__container {
  width: 93%;
  max-width: 1240px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  padding-top: 117px;
  padding-bottom: 55px;
  position: relative;

  &::before {
    content: "";
    width: 46px;
    height: 38px;
    background-image: url("../img/matrix.png");
    background-repeat: no-repeat;
    background-size: 46px 38px;
    position: absolute;
    transform: translateX(-50%);
    top: 54px;
    left: 50%;
  }
}

.projects__title {
  font-size: 42px;
  line-height: 42px;
  font-weight: 400;
  font-style: normal;
  color: #4f4f4f;
  width: 80%;
  margin: 0;
  margin-bottom: 16px;
  text-align: center;
}

.projects__text {
  font-size: 25px;
  line-height: 25px;
  font-weight: 300;
  font-style: normal;
  color: #4f4f4f;
  width: 80%;
  margin-bottom: 63px;
  text-align: center;
  letter-spacing: 0.5px;
}

.projects__list {
  list-style: none;
  width: 80%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.projects__item {
  width: 262px;
  margin-bottom: 20px;
  padding-bottom: 17px;
  background-color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  & img {
    width: 262px;
    height: 343px;
    margin-bottom: 25px;
  }
}

.projects__item-title {
  font-size: 21px;
  line-height: 21px;
  font-weight: 400;
  font-style: normal;
  color: #4f4f4f;
  margin: 0;
  margin-bottom: 7px;
  width: 80%;
  text-align: center;
}

.projects__description {
  font-size: 17px;
  line-height: 20px;
  font-weight: 300;
  font-style: normal;
  color: #808080;
  width: 91%;
  margin-bottom: 25px;
  text-align: center;
}

.projects__link {

  &:hover svg {
    opacity: 0.6;
  }

  &:active svg {
    opacity: 0.3;
  }
}

@media (max-width: $tablet-width) {
  .projects__container {
    width: 95%;
    padding-top: 100px;

    &::before {
      top: 32px;
    }
  }

  .projects__title {
    font-size: 35px;
  }

  .projects__text {
    margin-bottom: 40px;
  }

  .projects__list {
    width: 100%;
    justify-content: space-around;
  }

  .projects__item {
    width: 301px;

    & img {
      width: 301px;
      height: auto;
    }
  }

  .projects__description {
    width: 95%;
  }
}

@media (max-width: $mobile-width) {
  .projects__container {
    padding-bottom: 45px;
  }

  .projects__title {
    font-size: 32px;
  }

  .projects__text {
    font-size: 22px;
    width: 100%;
  }

  .projects__list {
    flex-direction: column;
    align-items: center;
  }

  .projects__item {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
